export default {
  name: 'AddBatch',
  methods: {
    clear: function () {
      this.rfid = ''
      this.drugname = ''
      this.dosage = ''
      this.amount = ''
      this.organization = ''
      this.manufacture_date = ''
      this.exprie_date = ''
    },
    addBatch: function () {
      this.$axios.post(`/api/addbatch`, {
        rfid: this.rfid,
        drugname: this.drugname,
        dosage: this.dosage,
        amount: this.amount,
        organization: this.organization,
        manufacture_date: this.manufacture_date,
        exprie_date: this.exprie_date
      }).then((resp) => {
        console.log(resp)
        this.$q.notify({
          message: 'Batch added successfully',
          color: 'green'
        })
      }).catch(err => {
        console.log(err)
        this.$q.notify({
          message: 'Failed to add batch',
          color: 'red'
        })
      })
    }
  },
  data () {
    return {
      rfid: '',
      drugname: '',
      dosage: '',
      amount: '',
      organization: '',
      manufacture_date: '',
      exprie_date: ''
    }
  }
}
